<template>
    <u-donut-chart :data="data" tooltip unit=""></u-donut-chart>
</template>

<script>
export default {
    data() {
        return {
            data: [
                { percent: 40, name: '', value: 40, state: 'nos' },
                { percent: 20, name: '', value: 20, state: 'nce' },
                { percent: 30, name: '', value: 30, state: 'nvm' },
                { percent: 10, name: 'IP', value: 10, state: 'net' },
            ],
        };
    },
};
</script>



