<template>
<u-line-chart border legend :x-axis="xaxis" :y-axis="yaxis" :series="series" :data="data" :smooth="smooth">
	<div slot="titleTemplate">{{title}}</div>
</u-line-chart>
</template>

<script>
export default {
	data() {
		return {
			title: '',
			xaxis: { key: 'week' },
			yaxis: { min: 0, name: ''},
			series: [{key: 'number'},{key: 'num', hidden: true } ],
			data: [
				{ week: '', number: 150, num: 1200 },
				{ week: '', number: 300, num: 1200 },
				{ week: '', number: 28, num: undefined },
				{ week: '', number: 200, num: 2000 },
				{ week: '', number: 74, num: 740 },
				{ week: '', number: 532, num:2000 },
				{ week: '', number: 420 ,num: 5000},
			],
			smooth: true,
		}
	},
};
</script>



