<template>
<u-xbar-chart border legend order="asc" :title="title" :x-axis="xaxis" :y-axis="yaxis" :series="series" :data="data">
    <div slot="tooltip" slot-scope="scope">
        {{ scope.row[scope.sery.key]}}
    </div>
</u-xbar-chart>
</template>

<script>
export default {
    data() {
        return {
            title: '',
            yaxis: { key: 'week' },
            xaxis: { min: 0 },
            series: [{key: 'number', name: '' }],
            data: [{ week: '', number: 150 }, { week: '', number: 300 }, { week: '', number: 28 }, { week: '', number: 200 }, { week: '', number: 74 }, { week: '', number: 532 }, { week: '', number: 420 }],
        }
    }
};
</script>



