<template>
<u-line-chart border legend :x-axis="xaxis" :y-axis="yaxis" :series="series" :data="data" :smooth="smooth" @click="clickPoint">
	<div slot="titleTemplate">{{title}}</div>
	<span slot="legendTemplate" slot-scope="scope">
		<template>{{scope.sery.name || scope.sery.key}}</template>
	</span>
	<template slot="legendSuffix">
		<span style="vertical-align: middle;"></span>
	</template>
</u-line-chart>
</template>

<script>
export default {
	data() {
		return {
			title: '',
			xaxis: { key: 'week' },
			yaxis: { min: 0, name: ''},
			series: [{key: 'number'},{key: 'num' } ],
			data: [
				{ week: '', number: 150, num: 1200 },
				{ week: '', number: 300, num: 1200 },
				{ week: '', number: 28, num: 1000 },
				{ week: '', number: 200, num: 2000 },
				{ week: '', number: 74, num: 740 },
				{ week: '', number: 532, num:2000 },
				{ week: '', number: 420 ,num: 5000},
			],
			smooth: true,
		}
	},
	methods: {
		clickPoint(e) {
			console.log(e);
		},
	},
};
</script>



