<template>
<u-bar-chart border legend gap-size="medium" :title="title" :x-axis="xaxis" :y-axis="yaxis" :series="series" :data="data"></u-bar-chart>
</template>

<script>
export default {
	data() {
		return {
			title: '',
			xaxis: { key: 'week' },
			yaxis: { min: 0 },
			series: [{key: 'number', name: '' }],
			data: [
				{ week: '', number: 150 },
				{ week: '', number: 300 },
				{ week: '', number: 28 },
				{ week: '', number: 200 },
				{ week: '', number: 74 },
				{ week: '', number: 532 },
				{ week: '', number: 420 },

			],
		}
	}
};
</script>



